___CSS_LOADER_EXPORT___.push([module.id, `:root {
  line-height: 1.5;
}

h1,
h2,
h3,
h4,
h5,
figure,
p,
ol,
ul {
  margin: 0;
}

ol[role="list"],
ul[role="list"] {
  list-style: none;
  padding-inline: 0;
}

h1,
h2,
h3,
h4,
h5 {
  font-size: inherit;
  font-weight: inherit;
}

img {
  display: block;
  max-inline-size: 100%;
}

/* Normalize end */
body {
  min-height: 100vh;
  display: flex;
  padding-top: 20vh;
  justify-content: center;
}

h1 {
  font-size: 40px;
  font-weight: bold;
  text-align: center;
  margin-bottom: 20px;
}

.main {
  width: fit-content;
}

form {
  display: flex;
  gap: 10px;
}

form input {
  flex-grow: 1;
}

.result-wrapper {
  display: none;
}

.result {
  margin-top: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 15px;
}

.result .temp {
  font-size: large;
  font-weight: bold;
}

.result .temp,
.result .icon {
  justify-self: right;
}

.result .icon {
  width: 70px;
}

.result .unit-toggle {
  grid-column: span 2;
  display: flex;
  justify-content: center;
  gap: 20px;
  border: none;
}

.fahrenheit {
  display: none;
}

.error-message {
  margin-top: 20px;
  padding: 10px;
  background-color: lightcoral;
  display: none;
}

.loading-icon {
  margin-top: 20px;
  padding: 10px;
  background-color: lightblue;
  display: none;
}
